<template>
  <div class="auth component-wrapper">
    <div class="window-wrapper">
      <div class="logo">
       <img class="logo-image" src="@/assets/newImages/logo.svg" alt="logo" />
      </div>
      <p class="text">
        {{ $localizeFilter('enter_new_password') }}
      </p>
      <form @submit.prevent="submitForm" class="login-form" id="login-form">
        <input v-model="password" type="password" id="password" name="password"
          :placeholder="$localizeFilter('password')" :class="{
            'error': v$.password.$error,
          }" />
        <span v-for="error in v$.password.$errors" :key="error.$uid" class="label-error">
          {{ error.$message }}
        </span>
        <button type="submit" class="button" id="submitButton" @click="submitHandler">
          {{ $localizeFilter("restore_password") }}
        </button>
      </form>
    </div>
</div>
</template>


<script>
import useVuelidate from "@vuelidate/core";
import { required, helpers } from "@vuelidate/validators";
import { defineComponent } from "@vue/composition-api";
import localizeFilter from "@/filters/localize.filter"

export default defineComponent({
  props: {
    token: {
      type: String,
      required: true
    }
  },
  setup() {
    return { v$: useVuelidate() };
  },
  data() {
    return {
      password: "",
      buttonKey: 1
    };
  },
  validations() {
    return {
      password: { 
        required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
      },
    };
  },
  methods: {
    async submitHandler() {
      this.v$.$touch();
        if (this.v$.$error) {
          setTimeout(()=>{
            this.buttonKey+=1
          }, 1000)
          return;
        }
        const el = document.getElementById('submitButton'); 
        el.classList.add("button--loading");
        try {
          await this.$store.dispatch('restorePassword', {newPassword: this.password, code: this.token})
          setTimeout(()=>{
            el.classList.remove("button--loading")
            this.$message(localizeFilter('restore_password_successfull'))
            this.$router.push('/login')
          },1000)
        } catch (e) {
             setTimeout(()=>{
              this.$error(e)
              el.classList.remove("button--loading")
              this.buttonKey+=1
            }, 1000)
        }
    },
  },
  computed: {
  },
});
</script>

